// src/utils/naming-conventions.ts
var toKebabCase = (str) => {
  return str.split(" ").map((part, index) => {
    if (index === 0) {
      return part.toLowerCase();
    }
    return part.charAt(0).toLowerCase() + part.slice(1);
  }).join("-");
};
var toPascalCase = (str) => {
  const words = str.match(/[a-z0-9]+/gi) || [];
  return words.map(
    (word) => word.charAt(0).toUpperCase() + word.substring(1).toLowerCase()
  ).join("");
};

// src/utils/find-metadata.ts
var findWallet = ({
  wallet
}) => {
  const walletObj = wallets.find(
    (w) => w.id.toLowerCase() === toKebabCase(wallet) || w.name.toLowerCase() === wallet.toLowerCase()
  );
  return walletObj;
};
var findToken = ({
  symbol,
  address,
  network
}) => {
  if (symbol) {
    return tokens.find(
      (token) => token.symbol.toLowerCase() === symbol.toLowerCase()
    );
  } else if (address && network) {
    return tokens.find(
      (token) => token.addresses[network]?.toLowerCase() === address.toLowerCase()
    );
  }
  return void 0;
};
var findNetwork = ({
  network,
  chainId
}) => {
  if (network) {
    const networkObj = networks.find(
      (net) => net.id.toLowerCase() === toKebabCase(network) || net.name.toLowerCase() === network.toLowerCase() || net.shortName?.toLowerCase() === network.toLowerCase()
    );
    return networkObj;
  }
  if (chainId) {
    const networkObj = networks.find((net) => net.chainId === chainId);
    return networkObj;
  }
  return void 0;
};

