import { jsx } from "react/jsx-runtime";
var DynamicIcon = forwardRef(
  ({ size, color, className, variant, fallback, metadata, type }, ref) => {
    const [IconComponent, setIconComponent] = useState(
      null
    );
    useEffect(() => {
      const loadIcon = async () => {
        let importFunction;
        if (!metadata) {
          if (!fallback)
            return null;
          setIconComponent(
            /* @__PURE__ */ jsx(
              BaseIcon,
              {
                ref,
                size,
                color,
                className,
                fallback
              }
            )
          );
          return;
        }
        switch (type) {
          case "token":
            importFunction = TOKEN_ICON_IMPORT_MAP[`Token${metadata.symbol.toUpperCase()}`];
            break;
          case "network":
            importFunction = NETWORK_ICON_IMPORT_MAP[`Network${toPascalCase(metadata.name)}`] ?? NETWORK_ICON_IMPORT_MAP[`Network${toPascalCase(metadata.id)}`];
            break;
          case "wallet":
            importFunction = WALLET_ICON_IMPORT_MAP[`Wallet${toPascalCase(metadata.name)}`] ?? WALLET_ICON_IMPORT_MAP[`Wallet${toPascalCase(metadata.id)}`];
            break;
          default:
            throw new Error(`invalid type ${type}`);
        }
        if (importFunction) {
          try {
            const { default: ImportedIcon } = await importFunction();
            setIconComponent(
              /* @__PURE__ */ jsx(
                ImportedIcon,
                {
                  ref,
                  size,
                  color,
                  className,
                  variant
                }
              )
            );
          } catch (error) {
            if (fallback) {
              setIconComponent(
                /* @__PURE__ */ jsx(
                  BaseIcon,
                  {
                    ref,
                    size,
                    color,
                    className,
                    fallback
                  }
                )
              );
            }
          }
        } else {
          if (fallback) {
            setIconComponent(
              /* @__PURE__ */ jsx(
                BaseIcon,
                {
                  ref,
                  size,
                  color,
                  className,
                  fallback
                }
              )
            );
          }
        }
      };
      loadIcon();
    }, [ref, size, color, className, variant, fallback]);
    return IconComponent;
  }
);

// src/TokenIcon.tsx
import { jsx as jsx2 } from "react/jsx-runtime";
var TokenIcon = forwardRef2(
  ({
    symbol,
    size,
    className,
    variant = "mono",
    color,
    address,
    network,
    fallback
  }, ref) => {
    const metadata = findToken(symbol ? { symbol } : { address, network });
    return /* @__PURE__ */ jsx2(
      DynamicIcon,
      {
        type: "token",
        metadata,
        className,
        color,
        fallback,
        ref,
        size,
        variant
      }
    );
  }
);
TokenIcon.displayName = "TokenIcon";

